// app/about/page.tsx
import React from "react";
import { FaUserMd, FaHospital, FaHeartbeat, FaCalendarCheck } from "react-icons/fa";

export default function AboutPage() {
  return (
    <main className="max-w-6xl mx-auto px-4 py-10"> 
      <h1 className="text-4xl font-bold mb-6 text-blue-800">O nas</h1>

      {/* Wstęp */}
      <section className="mb-10">
        <p className="text-lg text-gray-700 mb-4">
          Miejski Szpital Ogólny zapewnia najwyższej jakości usługi opieki zdrowotnej od 1985 roku. Nasza misja to
          dostarczanie nowoczesnej i kompleksowej opieki z troską, szacunkiem i zaangażowaniem.
        </p>
        <p className="text-lg text-gray-700">
          Korzystamy z najnowocześniejszej technologii medycznej oraz indywidualnego podejścia do każdego pacjenta.
        </p>
      </section>

      {/* Dlaczego warto */}
      <section className="mb-10">
        <h2 className="text-2xl font-semibold mb-4 text-blue-700">Dlaczego warto wybrać nasz szpital?</h2>
        <ul className="list-disc list-inside text-gray-700 space-y-2">
          <li>Doświadczeni i certyfikowani lekarze specjaliści</li>
          <li>Nowoczesny sprzęt medyczny</li>
          <li>Całodobowe usługi ratunkowe (24/7)</li>
          <li>Spersonalizowana opieka nad pacjentem</li>
          <li>Setki zadowolonych pacjentów każdego roku</li>
        </ul>
      </section>

      {/* Oddziały */}
      <section className="mb-10">
        <h2 className="text-2xl font-semibold mb-4 text-blue-700">Nasze oddziały</h2>
        <p className="text-gray-700 mb-2">Oferujemy kompleksową opiekę w ramach wielu specjalizacji:</p>
        <ul className="list-disc list-inside text-gray-700 space-y-2">
          <li>Kardiologia</li>
          <li>Neurologia</li>
          <li>Chirurgia ogólna</li>
          <li>Pediatria</li>
          <li>Ortopedia</li>
        </ul>
      </section>

      {/* Statystyki */}
      <section className="mb-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6 text-center">
        {[
          { icon: <FaUserMd className="text-4xl text-blue-600 mx-auto mb-2" />, label: "Lekarzy", value: "120+" },
          { icon: <FaHospital className="text-4xl text-blue-600 mx-auto mb-2" />, label: "Oddziałów", value: "15" },
          { icon: <FaHeartbeat className="text-4xl text-blue-600 mx-auto mb-2" />, label: "Pacjentów rocznie", value: "20,000+" },
          { icon: <FaCalendarCheck className="text-4xl text-blue-600 mx-auto mb-2" />, label: "Lat doświadczenia", value: "40" },
        ].map((stat, idx) => (
          <div key={idx} className="bg-blue-50 p-6 rounded-lg shadow hover:scale-105 transition-transform duration-500">
            {stat.icon}
            <p className="text-xl font-bold">{stat.value}</p>
            <p className="text-gray-600">{stat.label}</p>
          </div>
        ))}
      </section>

      {/* Zespół */}
      <section className="mb-14">
        <h2 className="text-2xl font-semibold mb-6 text-blue-700">Nasz zespół</h2>
        <div className="grid gap-6 sm:grid-cols-2 md:grid-cols-3">
          {[
            { name: "Dr Anna Nowak", role: "Kardiolog", img: "/images/doctor1.jpg" },
            { name: "Dr Jan Kowalski", role: "Chirurg", img: "/images/doctor2.jpg" },
            { name: "Dr Ewa Zielińska", role: "Pediatra", img: "/images/doctor3.jpg" },
          ].map((doc, idx) => (
            <div key={idx} className="bg-white rounded-xl shadow-md p-4 hover:shadow-xl transition-shadow duration-500">
              <img src={doc.img} alt={doc.name} className="w-full h-48 object-cover rounded-lg mb-4" />
              <h3 className="text-lg font-bold text-blue-800">{doc.name}</h3>
              <p className="text-gray-600">{doc.role}</p>
            </div>
          ))}
        </div>
      </section>

      {/* Nagłe przypadki */}
      <section className="bg-red-50 p-6 rounded-xl shadow-inner hover:scale-105 transition-transform duration-700">
        <h2 className="text-2xl font-semibold text-red-700 mb-2">Nagłe przypadki</h2>
        <p className="text-gray-700 mb-2">
          Nasz oddział ratunkowy działa 24 godziny na dobę, 7 dni w tygodniu. Zespół wykwalifikowanych lekarzy medycyny
          ratunkowej jest gotów nieść pomoc w każdej sytuacji.
        </p>
        <p className="text-xl font-bold text-red-600">Telefon alarmowy: +48 987 654 321</p>
      </section>

      {/* Call to action */}
      <section className="text-center mt-16">
        <h2 className="text-3xl font-bold text-blue-800 mb-4">Umów się na wizytę już dziś</h2>
        <p className="text-gray-600 mb-6">
          Skontaktuj się z nami telefonicznie lub poprzez formularz online. Zdrowie to Twój największy skarb!
        </p>
        <a
          href="/kontakt"
          className="inline-block bg-blue-700 text-white px-6 py-3 rounded-full text-lg font-semibold shadow-md hover:bg-blue-800 transition-colors"
        >
          Skontaktuj się z nami
        </a>
      </section>
    </main>
  );
}
